import React, { useContext, useEffect, useState } from "react";
import Modal from "react-bootstrap/Modal";
import { Button } from "@mui/material";
import "./inputFormModal.css";
import { Form } from "react-bootstrap";
import { CentralDataContext } from "../Context/CentralDataContext";
import Select from "react-select";

const TasksModal = ({ show, handleClose, Id }) => {
  const [formData, setFormData] = useState({
    taskName: "",
    taskDescription: "",
    requestedBy: "",
    assignedTo: "",
    startDate: "",
    endDate: "",
    status: "",
  });

  const { data, action } = useContext(CentralDataContext);

  const handleChange = (event) => {
    setFormData((prevFormData) => {
      return {
        ...prevFormData,
        [event.target.name]: event.target.value,
      };
    });
  };

  const handleSelectChange = (value, action) => {
    setFormData((prev) => ({
      ...prev,
      [action]: value,
    }));
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log("Submit");
  };

  const handleClosing = () => {
    handleClose();
    console.log("Closing");
  };

  const handleEditName = (id, name) => {
    console.log("editing");
    handleClosing();
  };

  useEffect(() => {
    if (Id !== null) {
      console.log("in");
      let task = data.find((d) => d.projectid === Id);
      console.log("tasks", task);
      const { taskdesc } = task;
      setFormData((prev) => ({ ...prev, taskDescription: taskdesc }));
    }
  }, [Id]);

  console.log("FORM DATA:", Id, formData);

  return (
    <>
      <Modal show={show} onHide={handleClosing} animation={true} centered>
        <Modal.Header closeButton>
          <Modal.Title>{action === "Add" ? "New" : "Edit"} Task</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Task Name</Form.Label>
              <Form.Control
                type="text"
                name="taskName"
                onChange={handleChange}
                placeholder="task name ...."
                value={formData.taskName}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Description</Form.Label>
              <Form.Control
                type="text"
                name="taskDescription"
                onChange={handleChange}
                placeholder="task Description..."
                value={formData.taskDescription}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Requested By</Form.Label>
              <Select
                name="requestedBy"
                placeholder={"Select members"}
                value={formData.requestedBy}
                onChange={(entry) => handleSelectChange(entry, "requestedBy")}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Assigned To</Form.Label>
              <Select
                name="assignedTo"
                placeholder={"Select members"}
                value={formData.assignedTo}
                onChange={(entry) => handleSelectChange(entry, "assignedTo")}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Start Date</Form.Label>
              <Form.Control
                type="date"
                name="startDate"
                onChange={handleChange}
                value={formData.startDate}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>End Date</Form.Label>
              <Form.Control
                type="date"
                name="endDate"
                onChange={handleChange}
                value={formData.endDate}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>status</Form.Label>
              <Select
                name="status"
                placeholder={"Select status"}
                value={formData.status}
                onChange={(entry) => handleSelectChange(entry, "status")}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClosing}>
            Close
          </Button>
          <Button
            variant="primary"
            onClick={
              Id === null ? handleSubmit : () => handleEditName(Id, "editing")
            }
          >
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
export default TasksModal;
