import React, { useContext, useEffect, useState } from "react";
import Modal from "react-bootstrap/Modal";
import { Button } from "@mui/material";
import "./inputFormModal.css";
import { Form } from "react-bootstrap";
import { CentralDataContext } from "../Context/CentralDataContext";
import Select from "react-select";
import CompanyList from "../Pages/CompanyList";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";

const PersonDataModal = ({ show, handleClose, Id }) => {
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    emailAdd: "",
    phoneNum: "",
    company: {
      value: "",
      label: "",
    },
  });
  const { handleAdd, data, action, handleEdit } = useContext(
    CentralDataContext
  );

  const handleSelectPhone = (event) => {
    console.log("heyy", event);
    setFormData((prev) => ({
      ...prev,
      phoneNum: event,
    }));
  };

  const handleSelectChange = (value, action) => {
    setFormData((prev) => ({
      ...prev,
      [action]: value,
    }));
  };

  const handleChange = (event) => {
    setFormData((prevFormData) => {
      return {
        ...prevFormData,
        [event.target.name]: event.target.value,
      };
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log("Submit");
    handleAdd(event, formData);
    handleClosing();
  };

  const handleClosing = () => {
    handleClose();
    setFormData({
      firstName: "",
      lastName: "",
      emailAdd: "",
      phoneNum: "",
      company: {},
    });
    console.log("Closing");
  };

  const handleEditName = (id, value) => {
    handleEdit(id, value);
    handleClosing();
    console.log("editing");
  };

  useEffect(() => {
    if (Id !== null) {
      console.log("in");
      let person = data.find((d) => d.id === Id);
      const { firstName, lastName, phoneNum, emailAdd, company } = person;
      console.log(person);
      setFormData((prev) => ({
        ...prev,
        firstName,
        lastName,
        emailAdd,
        phoneNum,
        company,
      }));
    }
  }, [Id]);

  let companyOptions = CompanyList.map((c) => ({
    value: c.CompanyName,
    label: c.CompanyName,
  }));

  console.log("FORM DATA:", Id, formData);
  return (
    <>
      <Modal show={show} onHide={handleClosing} animation={true} centered>
        <Modal.Header closeButton>
          <Modal.Title>{action === "Add" ? "New" : "Edit"} Person</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Firstname</Form.Label>
              <Form.Control
                type="text"
                name="firstName"
                onChange={handleChange}
                placeholder="Name..."
                value={formData.firstName}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Lastname</Form.Label>
              <Form.Control
                type="text"
                name="lastName"
                onChange={handleChange}
                placeholder="Name..."
                value={formData.lastName}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>email</Form.Label>
              <Form.Control
                type="email"
                name="emailAdd"
                onChange={handleChange}
                placeholder="email..."
                value={formData.emailAdd}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Phone No.</Form.Label>
              <PhoneInput
                placeholder="Enter phone number"
                value={formData.phoneNum}
                onChange={(e) => handleSelectPhone(e)}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Company</Form.Label>
              <Select
                name="company"
                placeholder={"Select Company"}
                options={companyOptions}
                value={formData.company}
                onChange={(entry) => handleSelectChange(entry, "company")}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClosing}>
            Close
          </Button>
          <Button
            variant="primary"
            onClick={
              Id === null ? handleSubmit : () => handleEditName(Id, formData)
            }
          >
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
export default PersonDataModal;
