import React, {
  createContext,
  useContext,
  useParams,
  useEffect,
  useState,
  useRef,
} from "react";
import CompanyList from "../Pages/CompanyList";
import RolesList from "../Pages/RolesList";
import { useLocation } from "react-router-dom";
import PersonsList from "../Pages/PersonsList";
import taskDetails from "../tasks/taskDetails";
import useHandleCheckBox from "../CustomHooks/useHandleCheckBox";
import ProjectData from "../card/cardList";

export const CentralDataContext = createContext();

const CentralDataProvider = (props) => {
  const [data, setData] = useState([]);
  const [page, setPage] = useState("");
  const [displayCheck, setDisplayCheck] = useState("none");
  const [delActive, setDelActive] = useState(false);
  const [action, setAction] = useState("Add");
  const [favouriteList, setFavouristeList] = useState([]);
  const { checked, setChecked, handleCheck } = useHandleCheckBox();
  const { pathname } = useLocation();
  const [team, setTeam] = useState([]);

  const isRender = useRef(true);
  useEffect(() => {
    console.log("URL params:", pathname.split("/")[1]);
    let pageName =
      pathname.split("/")[1][0].toUpperCase() + pathname.split("/")[1].slice(1);
    let dataToLoad =
      pageName.toLocaleLowerCase() === "company"
        ? CompanyList
        : pageName.toLocaleLowerCase() === "roles"
        ? RolesList
        : pageName.toLocaleLowerCase() === "task"
        ? taskDetails
        : pageName.toLocaleLowerCase() === "project"
        ? ProjectData
        : pageName.toLocaleLowerCase() === "team"
        ? team
        : PersonsList;

    if (
      (pageName.toLocaleLowerCase() === "project" ||
        pageName.toLocaleLowerCase() === "team") &&
      ProjectData.length !== 0
    ) {
      ProjectData.forEach((proj) => {
        console.log("in here");
        createTeam(proj.projectNum);
      });
    } else {
      setTeam([]);
      //setData(dataToLoad);
    }
    setData(dataToLoad);
    setPage(pageName);
    //console.log("in central context");
  }, [pathname]);

  console.log("teams:", team);

  const createTeam = (projectNum) => {
    let newTeam = {
      id: Math.floor(Math.random() * 100),
      projectId: projectNum,
      teamName: `team ${projectNum}`,
      teamDescription: "initial team",
      members: [
        {
          memberName: "john",
          role: "",
        },
        {
          memberName: "mary",
          role: "",
        },
        /* {
          memberName: "axel",
          role: "",
        },
        {
          memberName: "steven",
          role: "",
        },
        {
          memberName: "vicky",
          role: "",
        },*/
      ],
    };
    setTeam((prev) => [...prev, newTeam]);
  };

  const handleFavProj = (id) => {
    let proj = data.find((proj) => proj.projectNum === id);
    setFavouristeList((prev) => [...prev, proj]);
  };

  const handleDel = () => {
    if (!delActive) {
      setDisplayCheck("flex");
      setDelActive(true);
    } else {
      if (checked.length > 0) {
        console.log("handledel:", page);
        checked.forEach((check) => {
          if (page.toLocaleLowerCase() === "company") {
            setData((data) =>
              data.filter((d) => {
                return d.IdCompany !== check;
              })
            );
          } else if (page.toLocaleLowerCase() === "roles") {
            setData((data) =>
              data.filter((d) => {
                return d.id !== check;
              })
            );
          } else if (page.toLocaleLowerCase() === "person") {
            setData((data) =>
              data.filter((d) => {
                return d.id !== check;
              })
            );
          }
        });

        setDisplayCheck("none");
        setDelActive((prev) => !prev);
        setChecked([]);
      } else {
        setDisplayCheck("none");
        setDelActive((prev) => !prev);
      }
    }
  };

  const handleAdd = (event, value) => {
    console.log("adding");
    switch (page.toLocaleLowerCase()) {
      case "company":
        if (
          data.find(
            (d) =>
              d.CompanyName.toLocaleLowerCase() === value.toLocaleLowerCase()
          ) === undefined
        ) {
          setData((prev) => [
            ...prev,
            {
              IdCompany: Math.floor(Math.random() * 100),
              CompanyName: value
                .toLowerCase()
                .split(" ")
                .map((word) => {
                  return word.charAt(0).toUpperCase() + word.slice(1);
                })
                .join(" "),
            },
          ]);
        } else {
          alert("company already exist");
          event.preventDefault();
        }
        break;
      case "roles":
        if (
          data.find(
            (d) => d.roleName.toLocaleLowerCase() === value.toLocaleLowerCase()
          ) === undefined
        ) {
          setData((prev) => [
            ...prev,
            {
              id: Math.floor(Math.random() * 100),
              roleName: value
                .toLowerCase()
                .split(" ")
                .map((word) => {
                  return word.charAt(0).toUpperCase() + word.slice(1);
                })
                .join(" "),
            },
          ]);
        } else {
          alert("roles already exist");
          event.preventDefault();
        }
        break;

      case "person":
        if (
          data.find(
            (d) =>
              d.firstName.toLocaleLowerCase() ===
              value.firstName.toLocaleLowerCase()
          ) === undefined
        ) {
          setData((prev) => [
            ...prev,
            { ...value, id: Math.floor(Math.random() * 100) },
          ]);
        } else {
          alert("roles already exist");
          event.preventDefault();
        }
        break;
    }
  };

  const handleEdit = (id, value) => {
    console.log("before edit", data);

    if (page.toLocaleLowerCase() === "company") {
      setData((prev) =>
        prev.map((obj) =>
          obj.IdCompany === id ? { ...obj, CompanyName: value } : obj
        )
      );
    } else if (page.toLocaleLowerCase() === "roles") {
      setData((prev) =>
        prev.map((obj) => (obj.id === id ? { ...obj, roleName: value } : obj))
      );
    } else {
      setData((prev) => {
        let index = prev.findIndex((p) => p.id === id);
        prev[index] = value;
        return prev;
      });
    }
  };

  return (
    <CentralDataContext.Provider
      value={{
        data,
        displayCheck,
        checked,
        handleCheck,
        handleDel,
        handleAdd,
        handleEdit,
        setData,
        page,
        team,
        action,
        setAction,
        handleFavProj,
      }}
    >
      {props.children}
    </CentralDataContext.Provider>
  );
};
export default CentralDataProvider;
