import React, { useState, useContext } from "react";
import { useParams } from "react-router-dom";
import TasksList from "./taskDetails.js";
import "./taskCard.css";
import Grid from "@material-ui/core/Grid";
import "@szhsin/react-menu/dist/index.css";
import "@szhsin/react-menu/dist/transitions/slide.css";
import { useNavigate } from "react-router-dom";
import TaskCard from "./taskCard.js";
import { CentralDataContext } from "../Context/CentralDataContext";
import { BsSearch, BsPlus } from "react-icons/bs";
import { green } from "@material-ui/core/colors";
import Header from "../Header/Header.js";
import TasksModal from "../modal/TasksModal.js";
import useCommonState from "../CustomHooks/useCommonState";

export default function Task() {
  const { id } = useParams();
  const { setAction, handleFavProj } = useContext(CentralDataContext);
  const {
    show,
    id: Id,
    handleClose,
    handleShow,
    handleShowEdit,
  } = useCommonState(setAction);

  const navigate = useNavigate();

  const onClick = (id) => {
    console.log(id);
    navigate("/log/" + id);
  };

  let temp;
  let matches = TasksList.filter((t) => t.projectid === id);
  let title = matches.length > 0 ? `Project ${id}` : <></>;
  temp =
    matches.length > 0
      ? matches.map((t) => (
          <Grid item xs={12} s={8} md={6} lg={4}>
            <TaskCard
              tasksInfo={t}
              onClick={onClick}
              handleFav={handleFavProj}
              taskid={t.projectid}
            />
          </Grid>
        ))
      : (temp = TasksList.map((t) => (
          <Grid item xs={12} s={8} md={6} lg={4}>
            <TaskCard
              tasksInfo={t}
              onClick={onClick}
              taskid={t.projectid}
              handleShowEdit={handleShowEdit}
              handleFav={handleFavProj}
            />
          </Grid>
        )));

  return (
    <div>
      <Header
        handleShow={handleShow}
        name={id === undefined ? "Tasks" : title}
      />
      <div className="main-container">
        <Grid container spacing={2} className="grid-container">
          {temp}
        </Grid>
      </div>
      <TasksModal Id={Id} show={show} handleClose={handleClose} />
    </div>
  );
}
