import React, { useState } from "react";
import "./cardItem.css";
import Card from "react-bootstrap/Card";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import useContext from "react";
import { IconButton } from "@mui/material";
import StarBorderOutlinedIcon from "@mui/icons-material/StarBorderOutlined";
import StarOutlinedIcon from "@mui/icons-material/StarOutlined";

export default function ProjectCard({
  projectInfo,
  onClick,
  id,
  handleShowEdit,
  handleDel,
  handleFav,
}) {
  const [isFavourite, setIsFavourite] = useState(false);

  const handleFavouriteProj = (id) => {
    setIsFavourite((prev) => !prev);
    handleFav(id);
  };

  let star = isFavourite ? <StarOutlinedIcon /> : <StarBorderOutlinedIcon />;
  return (
    <div>
      <Card
        style={{
          maxHeight: "250px",
          cursor: "pointer",
          border: "none",
          borderRadius: "15px",
        }}
        className="card-temp"
      >
        <Card.Body className="card-body-temp">
          <Card.Title className="title">
            {projectInfo.projectName}
            {/* <div className="icons-container-project">
              <IconButton
                onClick={() => handleShowEdit(id)}
                size="small"
                aria-label="add"
              >
                <EditIcon />
              </IconButton>
              <IconButton
                onClick={() => handleDel(id)}
                size="small"
                aria-label="delete"
              >
                <DeleteIcon />
              </IconButton>
              <IconButton onClick={() => handleFavouriteProj(id)} size="small">
                {star}
              </IconButton>
      </div>*/}
          </Card.Title>
          <div className="card-main-container" onClick={() => onClick(id)}>
            <Card.Subtitle className="mb-3 mt-3">
              {projectInfo.projectNum}
            </Card.Subtitle>
            <div className="description">{projectInfo.projectDescription}</div>
            <div className="dates-container">
              <Card.Text className="links">
                Start Date:{" "}
                <span className="date">{projectInfo.startDate}</span>
              </Card.Text>
              <Card.Text className="links">
                End Date: <span className="date">{projectInfo.endDate}</span>
              </Card.Text>
            </div>
          </div>
        </Card.Body>
      </Card>
    </div>
  );
}
