import React, { useContext, useEffect, useState } from "react";
import Modal from "react-bootstrap/Modal";
import { Button } from "@mui/material";
import "./inputFormModal.css";
import { Form } from "react-bootstrap";
import { CentralDataContext } from "../Context/CentralDataContext";
import { ProjectContext } from "../Context/ProjectContext";
import ProjectData from "../card/cardList";
import RolesList from "../Pages/RolesList";
import Select from "react-select";
import ClearIcon from "@mui/icons-material/Clear";
import AddIcon from "@mui/icons-material/Add";
import { IconButton } from "@mui/material";

const TeamModal = ({ show, handleClose, Id }) => {
  const [formData, setFormData] = useState({
    projectId: "",
    teamName: "",
    teamDescription: "",
    members: [
      {
        memberName: "",
        role: "",
      },
    ],
  });
  const [count, setCount] = useState(2);

  const { action, data, team } = useContext(CentralDataContext);

  const handleChange = (event) => {
    setFormData((prevFormData) => {
      return {
        ...prevFormData,
        [event.target.name]: event.target.value,
      };
    });
  };

  const addMember = () => {
    setCount((prev) => prev + 1);
  };

  const removeMember = () => {
    setCount((prev) => prev - 1);
  };

  let roleOptions = RolesList.map((r) => ({
    value: r.roleName,
    label: r.roleName,
  }));
  /*
  const handleSubmit = (event) => {
    handleAddProject(event, formData);
    event.preventDefault();
    handleClosing();
    console.log("Submit");
  };
*/
  const handleClosing = () => {
    setFormData({
      projectId: "",
      teamName: "",
      teamDescription: "",
      members: [{ memberName: "", role: "" }],
    });
    handleClose();
    console.log("Closing");
  };
  /*
  const handleEditName = (id, value) => {
    handleEditProject(id, value);
    console.log("editing");
    handleClosing();
  };
*/
  console.log("FORM DATA:", Id, formData);

  useEffect(() => {
    if (Id !== null) {
      console.log("in", team);
      let teamSelected = team.find((t) => t.id === Id);
      console.log("team to edit:", teamSelected);
      const { projectId, teamName, teamDescription, members } = teamSelected;
      setFormData({ projectId, teamName, teamDescription, members });
      let { memberCount } = ProjectData.find(
        (proj) => proj.projectNum === projectId
      );
      //console.log("memberCount", memberCount);
      //setCount(memberCount);
    }
  }, [Id]);

  const processMemberElement = (count) => {
    let arr = [];

    for (let i = 0; i < count; i++) {
      arr[i] = (
        <div className="input-group">
          <Form.Group className=" member-group">
            {/*<Form.Control
                className="member"
                type="text"
                name="memberName"
                onChange={handleChange}
                placeholder="Name..."
                value={m.memberName}
              />*/}
            <Select
              className="member"
              /* value={{
                value: team[0].members[i].memberName,
                label: team[0].members[i].memberName,
              }}*/
              type="text"
              name="memberName"
            />
          </Form.Group>
          <Form.Group className="role-group">
            <Select
              name="role"
              options={roleOptions}
              value={formData.members[0].role}
            />
          </Form.Group>
          <IconButton onClick={() => removeMember()}>
            <ClearIcon className="clear-button" />
          </IconButton>
        </div>
      );
    }
    console.log("array", arr);
    return arr;
  };

  let membersElement =
    count === 1 ? (
      <div className="input-group">
        <Form.Group className=" member-group">
          {/*<Form.Control
                className="member"
                type="text"
                name="memberName"
                onChange={handleChange}
                placeholder="Name..."
                value={m.memberName}
              />*/}
          <Select
            className="member"
            /*value={{
              value: team[0].members[0].memberName,
              label: team[0].members[0].memberName,
            }}*/
            type="text"
            name="memberName"
          />
        </Form.Group>
        <Form.Group className="role-group">
          <Select
            name="role"
            options={roleOptions}
            value={formData.members[0].role}
          />
        </Form.Group>
        <IconButton onClick={() => removeMember()}>
          <ClearIcon className="clear-button" />
        </IconButton>
      </div>
    ) : (
      processMemberElement(count)
    );

  return (
    <>
      <Modal show={show} onHide={handleClosing} animation={true}>
        <Modal.Header closeButton>
          <Modal.Title>{action === "Add" ? "New" : "Edit"} Team</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Project Number</Form.Label>
              <Form.Control
                type="text"
                name="projectId"
                onChange={handleChange}
                placeholder="Project Number ...."
                value={formData.projectId}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Team Name</Form.Label>
              <Form.Control
                type="text"
                name="teamName"
                onChange={handleChange}
                placeholder="Project Name ..."
                value={formData.teamName}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Team Description</Form.Label>
              <Form.Control
                type="text"
                name="teamDescription"
                onChange={handleChange}
                placeholder="Project Description..."
                value={formData.teamDescription}
              />
            </Form.Group>
          </Form>

          <div>
            <div className="labels-input">
              <span>
                Member{" "}
                <IconButton onClick={() => addMember()}>
                  <AddIcon className="add-member-button" />
                </IconButton>
              </span>

              <span>Role</span>
            </div>

            {membersElement}
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClosing}>
            Close
          </Button>
          <Button variant="primary">Save Changes</Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
export default TeamModal;
