import * as React from "react";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";
import LogItem from "./LogItem";
import { useParams } from "react-router-dom";
import IconButton from "@mui/material/IconButton";
import "./tasklog.css";
import { useState } from "react";
import DeleteIcon from "@mui/icons-material/Delete";
import AddIcon from "@mui/icons-material/Add";
import InputFormModal from "../modal/inputFormModal";
import LogForm from "../inputform/logform";
import Header from "../Header/Header";

export default function LogList({ logs }) {
  const { id } = useParams();
  const [open, setOpen] = useState(false);
  const [display, setDisplay] = useState("none");
  const [checked, setChecked] = React.useState([]);
  const [edit, setEdit] = useState(false);
  const [log, setLogs] = useState([]);
  const [logValue, setLogValue] = React.useState({});

  const handleChange = (label, value) => {
    console.log(label, value);
    setLogValue((prevState) => ({
      ...prevState,
      username: "Vishnu",
      key: log.length,
      [label]: value,
    }));
  };

  const handleAddLog = () => {
    setLogs([...log, logValue]);
    setOpen(false);
  };

  const handleEditSelected = () => {
    setOpen(true);
    setEdit(true);
  };

  const handleToggle = (value) => () => {
    console.log("heyy:", value);
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];
    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    if (newChecked.length > 0) {
      setDisplay("flex");
    } else {
      setDisplay("none");
    }
    console.log(newChecked);
    setChecked(newChecked);
  };

  const handleDelete = () => {
    console.log("deleting...");

    checked.forEach((check) => {
      setLogs((previosLogs) =>
        previosLogs.filter((log) => {
          console.log("log:", log.key);
          console.log("check:", check);
          return log.key !== check;
        })
      );
    });

    setDisplay("none");
    setChecked([]);
  };

  const onClick = () => {
    setOpen(true);
  };

  const handleClosing = () => {
    setOpen(false);
    setEdit(false);
  };

  return (
    <div>
      <div className="project-bar">
        Task Title {id}
        <div className="icons">
          <IconButton
            onClick={onClick}
            className="delete-button"
            size="small"
            aria-label="add"
          >
            <AddIcon />
          </IconButton>
          <IconButton
            sx={{ display: { display } }}
            className="delete-button"
            size="small"
            aria-label="delete"
            onClick={handleDelete}
          >
            <DeleteIcon sx={{ alignSelf: "center" }} />
          </IconButton>
        </div>
      </div>
      {/*<Header handleShow={onClick} />*/}
      <List sx={{ width: "100%", bgcolor: "background.paper", padding: "0px" }}>
        {log.map((log) => {
          const labelId = `checkbox-list-label-${log.key}`;
          return (
            <div>
              <LogItem
                handleChecked={handleToggle}
                key={log.key}
                log={log}
                labelid={labelId}
                checked={checked}
                handleEditOption={handleEditSelected}
              ></LogItem>
              <Divider
                variant="inset"
                sx={{ borderColor: "white", borderBottomWidth: "medium" }}
                component="li"
              />
            </div>
          );
        })}
      </List>

      <InputFormModal
        formType={
          <LogForm
            handleChange={handleChange}
            logValue={logValue}
            initialState={{}}
          ></LogForm>
        }
        open={open}
        closing={handleClosing}
        edit={edit}
        handleAdd={handleAddLog}
      ></InputFormModal>
    </div>
  );
}
