import React, { createContext, useContext, useEffect, useState } from "react";
import { CentralDataContext } from "./CentralDataContext";

export const ProjectContext = createContext();

const ProjectProvider = (props) => {
  const { data, setData } = useContext(CentralDataContext);

  const handleAddProject = (event, value) => {
    if (!data.find((proj) => proj.projectid === value.projectNum)) {
      setData((prev) => [
        ...prev,
        {
          ...value,
          id: Math.floor(Math.random() * 100),
          key: Math.floor(Math.random() * 100),
        },
      ]);
    } else {
      alert("project already exist");
      event.preventDefault();
    }
  };

  const handleEditProject = (id, value) => {
    console.log("editing", id, value);
    setData((prev) => {
      let index = prev.findIndex((p) => p.projectNum === id);
      prev[index] = value;
      return prev;
    });
  };

  const handleDelProject = (id) => {
    console.log("deleting", id);
    let project = data.filter((proj) => proj.projectNum !== id);
    setData(project);
  };
  return (
    <ProjectContext.Provider
      value={{ handleAddProject, handleDelProject, handleEditProject }}
    >
      {props.children}
    </ProjectContext.Provider>
  );
};
export default ProjectProvider;
