import React, { useState, useContext } from "react";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import ProjectCard from "../card/cardItem";
import ProjectData from "../card/cardList";
import { makeStyles } from "@material-ui/core";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import Header from "../Header/Header";
import ProjectModal from "../modal/ProjectModal";
import { CentralDataContext } from "../Context/CentralDataContext";
import useCommonState from "../CustomHooks/useCommonState";
import { ProjectContext } from "../Context/ProjectContext";

const useStyles = makeStyles({
  gridContainer: {},
  gridItem: {
    padding: "25px",
    height: "100p%",
  },
});

export default function Project() {
  const { setAction, data, handleFavProj } = useContext(CentralDataContext);
  const { show, id, handleClose, handleShow, handleShowEdit } = useCommonState(
    setAction
  );
  const { handleDelProject } = useContext(ProjectContext);
  console.log("projects", data);
  const classes = useStyles();

  const navigate = useNavigate();

  const onClick = (id) => {
    console.log(id);
    navigate("/task/" + id);
  };

  let projectitems = data.map((proj) => {
    return (
      <Grid item className={classes.gridItem} xs={12} s={8} md={6} lg={4}>
        <ProjectCard
          key={proj.key}
          projectInfo={proj}
          onClick={onClick}
          id={proj.projectNum}
          handleShowEdit={handleShowEdit}
          handleDel={handleDelProject}
          handleFav={handleFavProj}
        />
      </Grid>
    );
  });

  return (
    <div>
      <Header handleShow={handleShow} name={"Projects"} />
      <Grid container className={classes.gridContainer}>
        {projectitems}
      </Grid>
      <ProjectModal Id={id} show={show} handleClose={handleClose} />
    </div>
  );
}
