import React, { useState } from "react";
import { useParams } from "react-router-dom";
import TasksList from "./taskDetails.js";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import "./taskCard.css";
import AvatarGroup from "react-avatar-group";
import Grid from "@material-ui/core/Grid";
import { Menu, MenuItem, MenuButton } from "@szhsin/react-menu";
import "@szhsin/react-menu/dist/index.css";
import "@szhsin/react-menu/dist/transitions/slide.css";
import { useNavigate } from "react-router-dom";
import { BsThreeDotsVertical, BsFillCalendarCheckFill } from "react-icons/bs";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import StarBorderOutlinedIcon from "@mui/icons-material/StarBorderOutlined";
import StarOutlinedIcon from "@mui/icons-material/StarOutlined";
import { IconButton } from "@mui/material";

export default function TaskCard({
  onClick,
  taskid,
  tasksInfo,
  handleShowEdit,
  handleFav,
}) {
  const [status, setStatus] = useState("Completed");

  let style = status === "on-site" ? "rgb(216, 167, 52)" : "green";
  const handleDelete = (id) => {
    console.log("deleting", id);
  };
  const [isFavourite, setIsFavourite] = useState(false);

  const handleFavouriteProj = (id) => {
    setIsFavourite((prev) => !prev);
    handleFav(id);
  };

  let star = isFavourite ? (
    <StarOutlinedIcon sx={{ color: "#ffaa00" }} />
  ) : (
    <StarBorderOutlinedIcon />
  );

  return (
    <Card className="task-card" key={tasksInfo.key}>
      <Card.Header className="card-header-modif">
        <h5 className="title">{tasksInfo.taskid}</h5>
        <div className="header-icon">
          <Menu
            menuButton={
              <MenuButton
                style={{ backgroundColor: style }}
                className="status-button"
              >
                {status}
              </MenuButton>
            }
            onItemClick={(e) => setStatus(e.value)}
            transition
          >
            <MenuItem value="on-site">On site</MenuItem>
            <MenuItem value="In-progress">In-progress</MenuItem>
            <MenuItem value="pending">Pending</MenuItem>
          </Menu>
          <IconButton onClick={() => handleFavouriteProj(taskid)} size="small">
            {star}
          </IconButton>
          <Menu
            menuButton={
              <MenuButton className="more-button">
                <BsThreeDotsVertical size="1.2rem"></BsThreeDotsVertical>
              </MenuButton>
            }
            onItemClick={(e) => {
              if (e.value === "edit") {
                handleShowEdit(taskid);
              } else {
                handleDelete(taskid);
              }
            }}
            transition
          >
            <MenuItem value="edit">
              <EditIcon sx={{ marginRight: "5px" }} />
              Edit
            </MenuItem>
            <MenuItem value="delete">
              <DeleteIcon sx={{ marginRight: "5px" }} />
              Delete
            </MenuItem>
          </Menu>
        </div>
      </Card.Header>
      <Card.Body
        style={{
          cursor: "pointer",
        }}
        className="card-background"
        onClick={() => onClick(taskid)}
      >
        <Card.Text>{tasksInfo.title}</Card.Text>
        <Card.Text>{tasksInfo.taskdesc}</Card.Text>
      </Card.Body>
      <Card.Footer className="footer-container card-background">
        <div className="dates-container-2">
          <BsFillCalendarCheckFill size="1.0rem"></BsFillCalendarCheckFill>
          <div className="dates">
            {tasksInfo.startdate.split("-").join("/")} -{" "}
            {tasksInfo.enddate.split("-").join("/")}
          </div>
        </div>
        <AvatarGroup
          avatars={["James Wang"]}
          initialCharacters={2}
          max={2}
          size={30}
          displayAllOnHover
          shadow={2}
        />
      </Card.Footer>
    </Card>
  );
}
