import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import Divider from "@mui/material/Divider";
import ListItemText from "@mui/material/ListItemText";
import ListItemAvatar from "@mui/material/ListItemAvatar";
import Avatar from "@mui/material/Avatar";
import EditIcon from "@mui/icons-material/Edit";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import Checkbox from "@mui/material/Checkbox";
import React, { useContext } from "react";
import { IconButton } from "@mui/material";
import { CentralDataContext } from "../Context/CentralDataContext";

function stringAvatar(name) {
  return name.split(" ").length > 1 && !(parseInt(name.split(" ")[1][0]) >= 0)
    ? { children: `${name.split(" ")[0][0]}${name.split(" ")[1][0]}` }
    : { children: `${name.split(" ")[0][0]}` };
}

const ListItems = ({ handleShow }) => {
  const { checked, handleCheck, data, displayCheck, page, team } = useContext(
    CentralDataContext
  );
  console.log("in list items:", team);
  let listItems = [];
  let Items = [];

  switch (page.toLocaleLowerCase()) {
    case "company":
      console.log("load company");
      Items = data.map(({ CompanyName: name, IdCompany: id }) => ({
        name,
        id,
      }));
      break;
    case "roles":
      console.log("load roles");
      Items = data.map(({ roleName: name, id: id }) => ({
        name,
        id,
      }));
      break;
    case "person":
      Items = data.map(({ firstName: name, lastName: lname, id: id }) => ({
        name,
        lname,
        id,
      }));
      break;
    case "team":
      Items = team.map(({ teamName: name, id: id }) => ({
        name,
        id,
      }));
      break;
    default:
      Items = [];
  }

  console.log(Items);
  listItems =
    Items.length > 0
      ? Items.sort((a, b) => a.name.localeCompare(b.name)).map((data) => {
          let name =
            page.toLocaleLowerCase() === "person"
              ? `${data.name} ${data.lname}`
              : data.name;
          return (
            <>
              <ListItem
                sx={{
                  backgroundColor: "#e6f0ff",
                  alignItems: "center",
                  borderRadius: 10,
                  gap: 0,
                }}
                alignItems="flex-start"
              >
                <ListItemIcon
                  sx={{ minWidth: 0, marginTop: 0, display: displayCheck }}
                >
                  <Checkbox
                    checked={checked.indexOf(data.id) !== -1}
                    onClick={() => handleCheck(data.id)}
                    edge="start"
                    disableRipple
                  />
                </ListItemIcon>

                <ListItemButton sx={{ padding: 0 }}>
                  <ListItemAvatar sx={{ marginTop: 0 }}>
                    <Avatar
                      sx={{
                        bgcolor: "#1976d2",
                        width: 40,
                        height: 40,
                        fontWeight: "bold",
                        padding: "25px",
                      }}
                      {...stringAvatar(name)}
                    />
                  </ListItemAvatar>
                  <ListItemText primary={name} />
                </ListItemButton>
                <IconButton onClick={() => handleShow(data.id)} size="small">
                  <EditIcon />
                </IconButton>
              </ListItem>
              <Divider
                sx={{ borderColor: "white", borderBottomWidth: "medium" }}
                variant="inset"
                component="li"
              />
            </>
          );
        })
      : [];

  return <>{listItems}</>;
};
export default ListItems;
