import React, { useState } from "react";

const useCommonState = (setAction) => {
  const [show, setShow] = useState(false);
  const [id, setId] = useState(null);

  const handleClose = () => {
    setAction("Add");
    setId(null);
    setShow(false);
  };

  const handleShow = () => {
    setShow(true);
  };

  const handleShowEdit = (id) => {
    console.log("id:", id);
    setId(id);
    setShow(true);
    setAction("Edit");
  };

  return { show, id, handleClose, handleShow, handleShowEdit };
};
export default useCommonState;
