import React, { useContext, useEffect, useState } from "react";
import Modal from "react-bootstrap/Modal";
import { Button } from "@mui/material";
import "./inputFormModal.css";
import { Form } from "react-bootstrap";
import { CentralDataContext } from "../Context/CentralDataContext";
import { ProjectContext } from "../Context/ProjectContext";

const ProjectModal = ({ show, handleClose, Id }) => {
  const [formData, setFormData] = useState({
    projectNum: "",
    projectName: "",
    projectDescription: "",
    startDate: "",
    endDate: "",
    //memberCount: "",
  });

  const { action, data } = useContext(CentralDataContext);
  const { handleAddProject, handleEditProject } = useContext(ProjectContext);

  const handleChange = (event) => {
    setFormData((prevFormData) => {
      return {
        ...prevFormData,
        [event.target.name]: event.target.value,
      };
    });
  };

  const handleSubmit = (event) => {
    handleAddProject(event, formData);
    event.preventDefault();
    handleClosing();
    console.log("Submit");
  };

  const handleClosing = () => {
    setFormData({
      projectNum: "",
      projectName: "",
      projectDescription: "",
      startDate: "",
      endDate: "",
      // memberCount: "",
    });
    handleClose();
    console.log("Closing");
  };

  const handleEditName = (id, value) => {
    handleEditProject(id, value);
    console.log("editing");
    handleClosing();
  };

  console.log("FORM DATA:", Id, formData);
  /*const formatdate = (value) => {
    let temp = value.split("-");

    return temp[2] + "-" + temp[1] + "-" + temp[0];
  };*/
  useEffect(() => {
    if (Id !== null) {
      console.log("in");
      let project = data.find((d) => d.projectNum === Id);
      console.log("found:", project);
      const {
        projectNum,
        projectName,
        projectDescription,
        startDate,
        endDate,
      } = project;
      console.log({
        projectNum,
        projectName,
        projectDescription,
      });
      setFormData({
        projectNum,
        projectName,
        projectDescription,
        startDate,
        endDate,
      });
    }
  }, [Id]);
  return (
    <>
      <Modal show={show} onHide={handleClosing} animation={true} centered>
        <Modal.Header closeButton>
          <Modal.Title>{action === "Add" ? "New" : "Edit"} Project</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Project Number</Form.Label>
              <Form.Control
                type="text"
                name="projectNum"
                onChange={handleChange}
                placeholder="Project Number ...."
                value={formData.projectNum}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Name</Form.Label>
              <Form.Control
                type="text"
                name="projectName"
                onChange={handleChange}
                placeholder="Project Name ..."
                value={formData.projectName}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Description</Form.Label>
              <Form.Control
                type="text"
                name="projectDescription"
                onChange={handleChange}
                placeholder="Project Description..."
                value={formData.projectDescription}
              />
            </Form.Group>
            {/* <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Number of Team Members</Form.Label>
              <Form.Control
                type="text"
                name="memberCount"
                onChange={handleChange}
                placeholder="number of member"
                value={formData.memberCount}
              />
  </Form.Group>*/}
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Start Date</Form.Label>
              <Form.Control
                type="date"
                name="startDate"
                onChange={handleChange}
                value={formData.startDate}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>End Date</Form.Label>
              <Form.Control
                type="date"
                name="endDate"
                onChange={handleChange}
                value={formData.endDate}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClosing}>
            Close
          </Button>
          <Button
            variant="primary"
            onClick={
              Id === null ? handleSubmit : () => handleEditName(Id, formData)
            }
          >
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
export default ProjectModal;
