import React, { useState, useContext, useEffect } from "react";
import { IconButton } from "@mui/material";
import { blue, white } from "@mui/material/colors";
import { makeStyles } from "@material-ui/core";
import DeleteIcon from "@mui/icons-material/Delete";
import AddIcon from "@mui/icons-material/Add";
import "./headerbar.css";
import ListContainer from "./ListContainer";
import { CentralDataContext } from "../Context/CentralDataContext";
import DataModal from "../modal/DataModal";
import Header from "../Header/Header";
import PersonDataModal from "../modal/PersonDataModal";
import useCommonState from "../CustomHooks/useCommonState";
import TeamModal from "../modal/TeamModal";

const Company = () => {
  const { page, setAction } = useContext(CentralDataContext);
  const { show, id, handleClose, handleShow, handleShowEdit } = useCommonState(
    setAction
  );

  console.log("id:", id);
  let Modal =
    page === "Company" || page === "Roles" ? (
      <DataModal Id={id} show={show} handleClose={handleClose} />
    ) : page == "Team" ? (
      <TeamModal Id={id} show={show} handleClose={handleClose} />
    ) : (
      <PersonDataModal Id={id} show={show} handleClose={handleClose} />
    );
  return (
    <>
      <Header handleShow={handleShow} name={page} />
      <ListContainer handleShow={handleShowEdit} />
      {Modal}
    </>
  );
};
export default Company;
