import React, { useContext, useEffect, useState } from "react";
import Modal from "react-bootstrap/Modal";
import { Button } from "@mui/material";
import "./inputFormModal.css";
import { Form } from "react-bootstrap";
import { CentralDataContext } from "../Context/CentralDataContext";

const DataModal = ({ show, handleClose, Id }) => {
  const [name, setName] = useState("");
  const { handleAdd, data, handleEdit, page, action } = useContext(
    CentralDataContext
  );

  let pageName = page.toLocaleLowerCase();

  let pageTitle =
    pageName === "company"
      ? "Company"
      : pageName === "roles"
      ? "Role"
      : "Person";

  let formName =
    pageName === "company"
      ? "CompanyName"
      : pageName === "roles"
      ? "roleName"
      : "PersonName";

  const handleChange = (event) => {
    setName(event.target.value);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (name === "") {
      alert("Please enter a name");
    } else {
      handleAdd(event, name);
      handleClosing();
    }
  };

  const handleClosing = () => {
    handleClose();
    setName("");
  };

  const handleEditName = (id, name) => {
    handleEdit(id, name);
    handleClosing();
  };

  useEffect(() => {
    if (Id !== null) {
      console.log("in");

      if (pageName === "company") {
        let company = data.find((d) => d.IdCompany === Id);
        const { CompanyName } = company;
        console.log(CompanyName);
        setName(CompanyName);
      } else if (pageName === "roles") {
        let role = data.find((d) => d.id === Id);
        const { roleName } = role;
        console.log(role);
        setName(roleName);
      } else {
        let person = data.find((d) => d.id === Id);
        const { PersonName } = person;
        console.log(person);
        setName(PersonName);
      }
    }
  }, [Id]);

  return (
    <>
      <Modal show={show} onHide={handleClosing} animation={true} centered>
        <Modal.Header closeButton>
          <Modal.Title>
            {action === "Add" ? "New" : "Edit"} {pageTitle}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="CustomeFilterNameInput">
              <Form.Label>Name</Form.Label>
              <Form.Control
                type="text"
                name={formName}
                onChange={handleChange}
                placeholder="Name..."
                value={name}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClosing}>
            Close
          </Button>
          <Button
            variant="primary"
            onClick={
              Id === null ? handleSubmit : () => handleEditName(Id, name)
            }
          >
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
export default DataModal;
