import { IconButton } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import DeleteIcon from "@mui/icons-material/Delete";
import AddIcon from "@mui/icons-material/Add";
import { makeStyles } from "@material-ui/core";
import { useLocation, useParams } from "react-router-dom";
import "../Pages/headerbar.css";
import { CentralDataContext } from "../Context/CentralDataContext";

const useStyles = makeStyles({
  buttonicon: { alignSelf: "center", color: "black" },
});

export default function Header({ handleShow }) {
  const classes = useStyles();
  const { handleDel } = useContext(CentralDataContext);
  const { pathname } = useLocation();
  const { id } = useParams();

  let isTaskPage = pathname.split("/").includes("task");
  let isLogPage = pathname.split("/").includes("log");

  let title =
    isTaskPage && id !== undefined
      ? `Project ${id}`
      : isLogPage && id !== undefined
      ? `Task ${id}`
      : pathname.split("/")[1][0].toUpperCase() +
        pathname.split("/")[1].slice(1);

  let style =
    title === "Project"
      ? {
          fontSize: "20px",
          margin: "auto",
        }
      : {
          fontSize: "20px",
          textAlign: "center",
          width: isTaskPage ? "82%" : "60.5%",
        };

  let display = title === "Task" || isLogPage || isTaskPage ? "none" : "flex";

  console.log("display:", title, display);

  return (
    <div className="header-container">
      <div style={style}>Hello tab {title}</div>
      {title !== "Project" && (
        <div className="icons-container">
          <IconButton onClick={handleShow} size="small" aria-label="add">
            <AddIcon className={classes.buttonicon} />
          </IconButton>
          <IconButton
            sx={{ display: { display } }}
            onClick={handleDel}
            size="small"
            aria-label="delete"
          >
            <DeleteIcon className={classes.buttonicon} />
          </IconButton>
        </div>
      )}
    </div>
  );
}
